.page {
  font-family: Inter, sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
  
  &__body {
    margin: 0;
    &--with-menu {
      overflow: hidden;
    }
  }
  
  h1 {
    margin: 0;
    padding: 0;
  }
  
  ul {
    padding: 0;
  }
  
  &__section {
    margin-bottom: 120px;
  }
  
  &__section-title {
    margin-bottom: 48px;
  
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $darkGray;
  
  }
  
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform $transitionTime;
  
    &:target {
      transform: translateX(0);
    }
  }
}
