.top-actions {
  --iconSize: 18px;
  
  display: grid;
  grid-template-columns: 1fr var(--iconSize) var(--iconSize);
  justify-items: center;
  align-items: center;
  grid-gap: 30px;
  
  @include onTablet {
    grid-gap: 40px;
    --iconSize: 24px;
  }
  
  &__logo {
    justify-self: start;
  }
}
