.products {
  &__area {
    margin-bottom: 40px;
  
    @include onTablet {
      bottom: 64px;
    }
  }
  
  &__image {
    max-width: 100%;
    height: 100%;
  
    object-fit: cover;
    object-position: center;
    transition: transform $transitionTime;
  
    &:hover {
      transform: (scale(105%));
    }
  }
  
  &__subtitle {
    font-size: inherit;
  
    @include onTablet {
      font-size: 24px;
    }
  }
}
