.menu {
  background-color: $lightGray;
  
  &__top-actions {
    margin-bottom: 55px;
    grid-template-columns: 1fr $iconSize;
  }
  
  &__content {
    height: 100vh;
    padding-top: 40px;
  
    box-sizing: border-box;
  
    @include onTablet {
      padding-top: 52px;
    }
  }
  
  &__list {
    padding: 0;
    margin-bottom: 40px;
  
    list-style: none;
  }
  
  &__item {
    margin-bottom: 24px;
  
    &:last-child {
      margin-bottom: 0;
    }
  }
  
  &__link {
    display: inline-block;
    padding-bottom: 8px;
  
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
  
    color: $darkGray;
    border-bottom: 1px solid transparent;
    transition: border-color $transitionTime;
  
    &:hover {
      border-color: $darkGray;
    }
  }
  
  &__call {
    display: block;
    width: max-content;
  
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 27px;
  
    color: $darkGray;
  
    &--phone {
      margin-bottom: 16px;
    }
  
    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $darkGray;
    }
  }
}
